import Vue from 'vue'
import Router from 'vue-router'
import IndexBody from '@/components/body/IndexBody'
import DownloadBody from '@/components/body/DownloadBody'
import DocumentBody from '@/components/body/DocumentBody'
import DevelopmentBody from '@/components/body/DevelopmentBody'
import AboutBody from '@/components/body/AboutBody'
import NewsBody from '@/components/body/NewsBody'
import Footer from '@/components/Footer'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'app',
      components: {
        default: IndexBody,
        Footer: Footer
      }
    }, {
      path: '/download',
      name: 'download',
      components: {
        default: DownloadBody,
        Footer: Footer
      }
    }, {
      path: '/document',
      name: 'document',
      components: {
        default: DocumentBody,
        Footer: Footer
      }
    }, {
      path: '/news',
      name: 'news',
      components: {
        default: NewsBody,
        Footer: Footer
      }
    }, {
      path: '/development',
      name: 'development',
      components: {
        default: DevelopmentBody,
        Footer: Footer
      }
    }, {
      path: '/about',
      name: 'about',
      components: {
        default: AboutBody,
        Footer: Footer
      }
    }
  ]
})



