<template>
  <div id="app">
    <div class="navbar-content">
      <div class="logo-title"><img src="./assets/images/panda.png" class="logo">BookOS</div>
      <nav class="navbar navbar-default" style="border-radius: 0px;" role="navigation">
        <div class="container-fluid">
          <div class="navbar-header">
            <button type="button" class="navbar-toggle" data-toggle="collapse"
                    data-target="#example-navbar-collapse">
              <span class="sr-only">切换导航</span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
              <span class="icon-bar"></span>
            </button>
            <a class="navbar-brand" href="#">BookOS</a>
          </div>
          <div  class="collapse navbar-collapse" id="example-navbar-collapse">
            <ul class="nav navbar-nav">
              <router-link to="/" tag="li" exact-active-class="active">
                <a>{{$t("navBar.index")}}</a>
              </router-link>
              <router-link to="/download" tag="li" exact-active-class="active">
                <a>{{$t("navBar.download")}}</a>
              </router-link>
              <router-link to="/document" tag="li" exact-active-class="active">
                <a>{{$t("navBar.document")}}</a>
              </router-link>
              <router-link to="/news" tag="li" exact-active-class="active">
                <a>{{$t("navBar.news")}}</a>
              </router-link>
              <router-link to="/development" tag="li" exact-active-class="active">
                <a>{{$t("navBar.development")}}</a>
              </router-link>
              <li @click="toggleLanguage">
                <a href="javascript:;">{{$t("navBar.language")}}</a>
              </li>
            </ul>
            <ul class="nav navbar-nav navbar-right">
              <router-link to="/about" tag="li" exact-active-class="active">
                <a href="#">{{$t("navBar.about")}}</a>
              </router-link>
            </ul>
          </div>
        </div>
      </nav>
    </div>
    <div style="margin:15px">
      <h4>{{$t("IndexBody.p1")}}</h4>
      <p style="text-indent: 3em;">{{$t("IndexBody.p2")}}</p>
    <hr/>
    </div>
    <router-view/>
    <router-view name="Footer" class="footer"/>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'App',
  $,
  data: function () {
    return {
    }
  },
  methods: {
    toggleLanguage: function (e) {
      if (this.$i18n.locale === 'zh') {
        localStorage.lang = 'en'
        this.$i18n.locale = 'en'
      } else {
        localStorage.lang = 'zh'
        this.$i18n.locale = 'zh'
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif,微软雅黑;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  min-height: 100%;
  position: relative;
  padding-bottom: 200px;
}
  .footer{
    background-color:#222222;
    height:200px;
    width:100%;
    bottom: 0;
    position: absolute;
    left: 0;
  }
.navbar-nav{
  font-size:18px;
}
.navbar-brand{
  font-size:25px;
}
.logo{
  width:64px;
  height:64px;
  margin-top:5px;
  opacity: 0.8;
}
.logo-title{
  font-size:35px;
  font-weight: bold;
  padding:5px;
  margin-left:25px;
}
html,body{
  height:100%;
}
</style>



